@import url(https://fonts.googleapis.com/css2?family=Calistoga&family=Crimson+Pro:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&family=Mali:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Beth+Ellen&family=Spectral:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,300;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,300;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bellota&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
  background-image: url('https://images.pexels.com/photos/255379/pexels-photo-255379.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500');
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5px;
}

input {
  margin: 5px;
  max-width: 5o0px;
}

#greeting {
    font-family: 'Calistoga', cursive;
    font-size: 40px;
}

#clock {
    font-family: 'Crimson Pro', serif;
}



#stroke {
    font-family: 'Mali', cursive;
    font-weight: bold;
}

#spam {
    font-family: 'Courier Prime', monospace;
}
#typein {
    font-family: 'Spectral', serif;
}

#reply {
    font-family: 'Beth Ellen', cursive;
    font-size: 24px;
}
#submit {
    margin-top: 10px;
    max-width: 150px;
    align-self: center;
}

#reginvite, #conditional {
    font-family: 'Jost', sans-serif;
    font-weight: bold;
}

#reg_form {
    display: inline-flex;
    flex-direction: column;
    font-family: 'Jost', sans-serif;
}
#submit {
    border: none;
    font-family: 'Jost', sans-serif;  
    font-size: 16px;
    width: 200%;  
    color: teal;
    background-color: wheat;
}

#submit:hover {
    color: wheat;
    background-color: teal;
}
#footer {
    font-family: 'Bellota', cursive;
}
